import React from 'react';
import Layout from './Components/Layout';
import MobileContainer from './Components/Container/MobileContainer';
import DesktopContainer from './Components/Container/DesktopContainer';
import { Responsive } from 'semantic-ui-react';


const getWidth = () => {
  const isSSR = typeof window === 'undefined';

  return isSSR ? Responsive.onlyTablet.minWidth : window.innerWidth;
};

const ResponsiveContainer = ({ children }) => (
  <div>
    <DesktopContainer {...getWidth}>{children}</DesktopContainer>
    <MobileContainer getWidth={getWidth}>{children}</MobileContainer>
  </div>
);

const HomepageLayout = () => (

    <ResponsiveContainer>
      <Layout />
    </ResponsiveContainer>

);

export default HomepageLayout;
