import React from 'react';
import {
    Button,
    Container,
    Header,
} from 'semantic-ui-react';


const HomepageHeading = ({ mobile }) => {
    
    return (
        <Container text id='home'>
            <Header
                as='h1'
                content='Yurii Levchenko'
                inverted
                style={{
                    fontSize: mobile ? '2em' : '4em',
                    fontWeight: 'normal',
                    marginBottom: 0,
                    marginTop: mobile ? '1.5em' : '3em',
                }}
            />
            <Header
                as='h2'
                content='Front-End Junior Developer'
                inverted
                style={{
                    fontSize: mobile ? '1.5em' : '1.7em',
                    fontWeight: 'normal',
                    marginTop: mobile ? '0.5em' : '1.5em',
                }}
            />
            <Button inverted as='a' href='#projects' primary size='huge'>
               My projects
            </Button>
        </Container>
    );
};
export default HomepageHeading;
  