import React from 'react';
import { Button, Card, Grid, Image } from 'semantic-ui-react';

const Project = ({ item }) => {
  const { title, desc, useTehnologies, image, gitUrl, projectUrl } = item;
  return (
    <Grid.Column
      className="project-card"
      style={{
        paddingBottom: '5em',
        paddingTop: '5em',
        display: 'flex',
        justifyContent: 'center',
      }}>
      <Card fluid>
        <Image src={image} wrapped ui={false} />
        <Card.Content>
          <Card.Header>{title}</Card.Header>
          <Card.Meta>{useTehnologies}</Card.Meta>
          <Card.Description>{desc}</Card.Description>
        </Card.Content>
        <Card.Content extra>
          <div className="ui two buttons">
            <Button
              as="a"
              target="_blank"
              rel="noopener noreferrer"
              href={gitUrl}
              basic
              color="green">
              Git
            </Button>
            <Button
              as="a"
              target="_blank"
              rel="noopener noreferrer"
              href={projectUrl}
              basic
              color="blue">
              Demo
            </Button>
          </div>
        </Card.Content>
      </Card>
    </Grid.Column>
  );
};
export default Project;
