import React, { Component } from 'react';
import HomepageHeading from '../../Header';
import {
  Button,
  Container,
  Icon,
  Menu,
  Responsive,
  Segment,
  Sidebar,
} from 'semantic-ui-react';

class MobileContainer extends Component {
  state = {};

  handleSidebarHide = () => this.setState({ sidebarOpened: false });

  handleToggle = () => this.setState({ sidebarOpened: true });

  render() {
    const { children, getWidth } = this.props;
    const { sidebarOpened } = this.state;

    return (
      <Responsive
        as={Sidebar.Pushable}
        getWidth={getWidth}
        maxWidth={Responsive.onlyMobile.maxWidth}>
        <Sidebar
          as={Menu}
          animation="push"
          inverted
          onHide={this.handleSidebarHide}
          vertical
          visible={sidebarOpened}>
          <Menu.Item as="a" href="#home">
            Home
          </Menu.Item>
          <Menu.Item as="a" href="#projects">
            Projects
          </Menu.Item>
          <Menu.Item as="a" href="#profile">
            Profile
          </Menu.Item>
          <Menu.Item as="a" href="#contacts">
            Contacts
          </Menu.Item>
        </Sidebar>

        <Sidebar.Pusher
          // dimmed={sidebarOpened}
        >
          <Segment
            inverted
            textAlign="center"
            vertical
            className="heading-image mobile">
            <Container>
              <Menu inverted pointing secondary size="large">
                <Menu.Item onClick={this.handleToggle}>
                  <Icon name="sidebar" />
                </Menu.Item>
                <Menu.Item position="right">
                <Button as="a" inverted href='#contacts'>
                    Contacts
                  </Button>
                </Menu.Item>
              </Menu>
            </Container>
            <HomepageHeading mobile />
          </Segment>

          {children}
        </Sidebar.Pusher>
      </Responsive>
    );
  }
}

export default MobileContainer;
