import React from 'react';
import Projects from './Projects/Projects';
import Footer from './Footer';
import {
  Container,
  Divider,
  Grid,
  Header,
  List,
  Segment,
} from 'semantic-ui-react';

const Layout = () => {
  return (
    <>
      <Segment style={{ padding: '8em 0em' }} vertical>
        <Grid container stackable verticalAlign="middle">
          <Grid.Row>
            <Grid.Column width={8}>
              <Header as="h3" style={{ fontSize: '2em' }} id="profile">
                Profile
              </Header>
              <p style={{ fontSize: '1.33em' }}>
              To my technical knowledge I can include: React, Redux, HTML5, CSS3, JavaScript, jQuery, have worked with API, have experience with the MERN stack. Have experience with works users interfaces Bootstrap, Semantic UI, Materialize. There is also knowledge and understanding of the OOP, the initial knowledge of programming in Java. Familiar with the MySQL database. Knowledge of the theory of soware testing. Familiar with the Git version control system.
              </p>
              <Header as="h3" style={{ fontSize: '2em' }}>
                Education
              </Header>
              <Header as="h4" color="grey">
                September 2010 – January 2017
              </Header>
              <p style={{ fontSize: '1.33em' }}>
                Master, computer-integrated technological processes and
                production, Kyiv National University of Technology and Design,
                Kiev
              </p>
              <Header as="h3" style={{ fontSize: '2em' }}>
                Courses
              </Header>
              <Header as="h4" color="grey">
                December 2019 – February 2020
              </Header>
              <p style={{ fontSize: '1.33em' }}>
                Front-End JavaScript Online Winter 2019, EPAM
              </p>
            </Grid.Column>
            <Grid.Column floated="right" width={8}>
              <Header as="h3" style={{ fontSize: '2em' }}>
                Employment History
              </Header>

              <Header as="h2" style={{ fontSize: '1.5em' }}>
                QA at «uTest»
              </Header>
              <Header as="h4" color="grey">
                February 2017 – March 2019
              </Header>
              <p style={{ fontSize: '1.33em' }}>
                Freelance testing of web pages, mobile applications and desktop
                applications
              </p>
              <Header as="h2" style={{ fontSize: '1.5em' }}>
                Master of KPPiA at LLC "M-Qadro"
              </Header>
              <Header as="h4" color="grey">
                February 2016 – October 2016
              </Header>
              <p style={{ fontSize: '1.33em' }}>
                Installation of equipment of automated production lines
              </p>
              <Header as="h2" style={{ fontSize: '1.5em' }}>
                Engineer of the brigade of telecommunication networks at Atracom
                Ltd., Kiev
              </Header>
              <Header as="h4" color="grey">
                December 2015 – February 2016
              </Header>
              <p style={{ fontSize: '1.33em' }}>
                -Internet connection
                <br />
                -To connect to the telephone network
              </p>
            </Grid.Column>
          </Grid.Row>

        </Grid>
      </Segment>

      <Projects />

      <Segment style={{ padding: '8em 0em' }} vertical id="about">
        <Container text>
          <Header as="h3" style={{ fontSize: '2em' }}>
            About Me
          </Header>
          <p style={{ fontSize: '1.33em' }}>
            In addition to the basic skills for work, I have such qualities as
            creative thinking towards the realization of the tasks set, I am not
            afraid to take responsibility, speed of learning, ability to find a
            common language with other people.
          </p>

          <Divider
            as="h4"
            className="header"
            horizontal
            style={{ margin: '3em 0em', textTransform: 'uppercase' }}>
            Contacts
          </Divider>

          <List size="big" id="contacts">
            <List.Item icon="users" content="Yurii Levchenko" />
            <List.Item icon="marker" content="Kyiv, UA" />
            <List.Item
              icon="phone"
              content={<a href="tel:+380933029243">+380933029243</a>}
            />
            <List.Item
              icon="mail"
              content={
                <a href="mailto:yurilev4@gmail.com">yurilev4@gmail.com</a>
              }
            />
            <List.Item
              icon="paper plane"
              content={<a href="tg://resolve?domain=lightnex">Telegram</a>}
            />
            <List.Item
              icon="github"
              content={<a target="_blank" rel="noopener noreferrer" href="https://github.com/FyuroTEX">GitHub</a>}
            />
          </List>
        </Container>
      </Segment>

      <Footer />
    </>
  );
};
export default Layout;
