import React from 'react';
import {
    Container,
    Grid,
    Header,
    List,
    Segment,
  } from 'semantic-ui-react';

export const Footer = () => {
    return (
        <Segment inverted vertical style={{ padding: '1em 0em' }}>
        <Container>
          <Grid divided inverted stackable>
            <Grid.Row>
              <Grid.Column textAlign='center'>
                <Header inverted as="h4" content="Portfolio" />
                <List link inverted>
                  <List.Item as="a">Yurii Levchenko 2020</List.Item>
                </List>
              </Grid.Column>
             
            </Grid.Row>
          </Grid>
        </Container>
      </Segment>
    )
}
