import React, { Component } from 'react';
import HomepageHeading from '../../Header';
import {
  Button,
  Container,
  Menu,
  Responsive,
  Segment,
  Visibility,
} from 'semantic-ui-react';


class DesktopContainer extends Component {
  state = {};

  hideFixedMenu = () => this.setState({ fixed: false });
  showFixedMenu = () => this.setState({ fixed: true });

  render() {
    const { children, getWidth } = this.props;
    const { fixed } = this.state;

    return (
      <Responsive getWidth={getWidth} minWidth={Responsive.onlyTablet.minWidth}>
        <Visibility
          once={false}
          onBottomPassed={this.showFixedMenu}
          onBottomPassedReverse={this.hideFixedMenu}>
          <Segment
            inverted
            textAlign="center"
            vertical
            className='heading-image desktop'
          >
            <Menu
              fixed={fixed ? 'top' : null}
              inverted={!fixed}
              pointing={!fixed}
              secondary={!fixed}
              size="large"
            >
              <Container>
                <Menu.Item as="a" href='#home'>
                  Home
                </Menu.Item>
                <Menu.Item as="a" href='#projects'>Projects</Menu.Item>
                <Menu.Item as="a" href='#profile'>Profile</Menu.Item>
                <Menu.Item position="right">
                  <Button as="a" href='#contacts' inverted={!fixed}>
                    Contacts
                  </Button>
                </Menu.Item>
              </Container>
            </Menu>
            <HomepageHeading />
          </Segment>
        </Visibility>

        {children}
      </Responsive>
    );
  }
}
export default DesktopContainer;
